export default {
    onLoad(option){
        this.getTxmxtxinfo(option.tx_id)
    },
    data(){
        return {
            txInfo:''
        }
    },
    methods: {
        getTxmxtxinfo(id){
            this.$http.getTxmxtxinfo({tx_id:id}).then(res=>{
                if(res.status == 200) {
                    this.txInfo = res.data
                }
            })
        }
    },
}
